.tech-icons svg {
    font-size: 3rem;
    margin-right: 1rem;
}

.tech-icons p {
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-section {
    height: 80vh;

}

.icon-btn a {
    font-size: 2rem;
    color: #171515;
}

.tech-icons .html {
    color: #e34c26;
}

.tech-icons .css {
    color: #264de4;
}

.tech-icons .bootstrap {
    color: #780ff1;
}

.tech-icons .js {
    color: #f0db4f;
}

.tech-icons .react {
    color: #61DBFB;
}

.tech-icons .mui {
    color: #007fff;
}

.tech-icons strong {
    font-size: 1.5rem;
    margin-right: 1rem;
}

.bg-black {
    background-color: #000 !important;
    color: #fff !important;
}

.circle {
    width: .4rem;
    height: .4rem;
    background: #fff;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    margin: auto;
    animation: circle 15s linear infinite;
}

.main-circle {
    position: relative;
    padding: 3px;
    background: linear-gradient(to right, #0074cc, #00ccff);
    border-radius: 50%;
    display: flex;
    /* animation: circleColor 15s linear infinite; */
}

@keyframes circle {
    0% {
        transform: rotate(0deg) translate(104px) rotate(0deg);
    }

    100% {
        transform: rotate(360deg) translate(104px) rotate(-360deg);
    }
}