#contact{
    height: 40vh;
    display: flex;
    justify-content: center;
}
.contact-info{
    float: left;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40%;
    border: 1px solid lightgray;
    padding: 1rem 1.5rem;
    font-size: 1.1rem;
    border-radius: 20px;
}
.contact-info svg{
    margin-right: .5rem;
    font-size: 1.3rem;
}
#myBtn {
    height: 50px;
    width: 50px;
    display: none;
    position: fixed;
    bottom: 5%;
    right: 5%;
    z-index: 99;
    border: none;
    outline: none;
    background: linear-gradient(to bottom, #0074cc, #00ccff);
    color: linear-gradient(to bottom, #0074cc, #00ccff);;
    cursor: pointer;
    padding: 15px;
    border-radius: 50%;
    font-size: 18px;
    padding-top: 17px;
    padding-left: 17px;
}

#myBtn:hover {
    --tw-scale-x: 1.25;
    --tw-scale-y: 1.25;
    background: linear-gradient(to top, #0074cc, #00ccff);
}
#myBtn {
    right: 2% ;
    bottom: 4% ;
    font-size: 1.7rem;
  }

  #myBtn svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }