#project{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 3rem;
}
.taskmanager-img{
    float: left;
}
.taskmanager-img img{
    height: 100%;
    width: 100%;
}
.image-tsk{
    background-image: url('../img/taskmanager.png');
    height: 335px;
    width: 470px;
    background-size: contain;
    background-position: left;
    background-repeat: no-repeat;
}
.image-elega{
    background-image: url('../img/elegawatch.png');
    /* background-size: cover; */
    background-size: 100% 100%;
    background-position: inherit;
}
.live-btn:hover{
    background: linear-gradient(to left, #0074cc, #00ccff) !important;
}
.live-btn{
    border: 1px solid lightgray !important;
    border-radius: 10px !important;
    padding: 0.5rem 1rem !important;
    text-align: center !important;
    margin: 1rem 0 !important;
    background: linear-gradient(to right, #0074cc, #00ccff) !important;
    transition: all .10s;
}
.responsive-project-img{
    display: none;
}
.elegawatch-img{
    max-width: fit-content !important;
}